import Layout from '../components/layout';

const AboutPage = () => {
  return (
    <Layout pageTitle="About Me">
      <p>고앵이, ywbird 입니다.</p>
      <p>프론트엔드 개발자, UX 디자이너를 꿈꾸는 개발자 지망생 입니다.</p>
      <br/>
      <p>이 사이트의 테마인 <a href="https://github.com/ywbird/lotus-gatsby-theme">lotus-gatsby-theme</a>을 ReactJS 공부차 만들고 있습니다.</p>
    </Layout>
  );
};

export const Head = () => <title>About Me</title>;

export default AboutPage;
